.footer{
    color:#d9d9d9;
    background-color: #323232;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:20px;
    padding: 20px 8vw;
    padding-top: 80px;
    margin-top: 10px;
}
.footer-content{
    width: 100%;
    display: flex;
    grid-template-columns: 2fr 2fr 2fr ;
    gap:80px;
}
.footer-content-left,.footer-content-right,.footer-content-centre{
   
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 30px;
}
.footer-content-left li,.footer-content-right li,.footer-content-centre li{
    list-style: none;
    margin-bottom: 10px;
    cursor: pointer;
}
.footer-content-left h2,.footer-content-right h2,.footer-content-centre h2{
    color: white;
    cursor: pointer;
}
.footer-social-icons img{
    width: 40px;
    margin-right: 15px;
    cursor: pointer;
}
.footer hr{
    width: 1000px;
    height: 2px;
    margin: 20px 0;
    background-color: #734d26;
    border: none;
}
@media (max-width:750px){
    .footer-content{
        display: flex;
        flex-direction: column;
        gap: 35px;
    }
    .footer-copyright{
        text-align: center;
    }
}
.footer-social-icons a{
    height: 100%;
    width: 100%;
    font-size: large;
}